.cart-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #d9f3ff;
    border-radius: 12px;
    font-family: Arial, sans-serif;
    display: grid;
    flex-direction: column;
    gap: 20px;
}

.cart-input {
    width: 100%;
    padding: 12px;
    font-size: 16px;
    border-radius: 8px;
    outline: none;
}

.cart{
    border-radius: 15px;
    padding: 40px;
    margin-top: 50px;
    background-color: white;
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(3, 1fr);
}

.cart-add-button {
    padding: 10px 25px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: 10px;
    width: 70%;
}

.search{
    padding: 15px;
    border-radius: 15px;
    background-color: white;
    display: flex;
    gap: 400px;
}

.cart-add-button:hover {
    background-color: #0056b3;
}

.cart-items {
    display: flex;

    width: 100%;
}

.cart-item {
    background-color: #fff;
    border-radius: 12px;
    border: 2px solid #ddd;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.2s ease;
}

.cart-item:hover {
    transform: translateY(-5px);
}

.cart-item-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 15px;
    text-align: center;
}

.cart-item-controls {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
}

.cart-button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 8px;
    width: 40px;
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.plus-minus{
    display: flex;
    text-align: center;
    margin-left: 55px;
}

.cart-button:hover {
    background-color: #0056b3;
}

.cart-item-count {
    font-size: 20px;
    font-weight: bold;
    margin: 0 10px;
}
